declare let window: {socket:SocketIOClient.Socket};
import * as React from 'react';
import './Weather.scss';

type stateType = {
    current: forecastType,
    forecast: forecastType[]
}

type forecastType = {
    "time": number,
    "summary": string,
    "icon": string,
    "precipIntensity": number,
    "precipProbability": number,
    "precipType": string,
    "temperature": number,
    "apparentTemperature": number,
    "dewPoint": number,
    "humidity": number,
    "pressure": number,
    "windSpeed": number,
    "windGust": number,
    "windBearing": number,
    "cloudCover": number,
    "uvIndex": number,
    "visibility": number,
    "ozone": number
}
const defaultState:stateType = {
    current: null,
    forecast : [] as forecastType[],
}


class Weather extends React.Component<{}, stateType> {
    
    constructor(props: {}, state: stateType) {
        super(props);
        this.state = defaultState;
        window.socket.on("weather", (weatherData:any) => {
            if(!weatherData.currently){
                return;
            }
            this.setState({
                current: weatherData.currently,
                forecast: weatherData.hourly.data,
            });       
        });
    }

    displayForecast(){
        return this.state.forecast.slice(1,9).map( (weather: forecastType) => {
            let hour: number = new Date((weather.time) * 1000).getHours();
            let timeString:string = `${hour}:00`;
            if (hour < 10){
                timeString = "0" + timeString;
            }
            return (
                <div className="weather" key = {weather.time}>
                    <div className="temperature"> {weather.temperature.toFixed(0)} &deg;C</div>
                    <div className="condition-icon"><img src={`/static/DarkSky/${weather.icon}.svg`} alt=""/></div>
                    <div className="time">{timeString}</div>
                </div>
            );
        });
    }

    render() {
        if(this.state.current == null || this.state.forecast.length == 0){
            return null;
        }
        return (
            <div className = "weather-container-container">
                <div className="weather-container">
                    <div className="now">
                        <div className="weather">
                            <div className="temperature"> {this.state.current.temperature.toFixed(0)} &deg;C</div>
                            <div className="condition-icon"><img src={`/static/DarkSky/${this.state.current.icon}.svg`} alt=""/></div>
                            <div className="time">{this.state.current.summary}</div>
                        </div>
                    </div>
                    <div className="forecast">
                        {this.displayForecast()}
                    </div>
                </div>
            </div>
        );
    }
};

export default Weather;