declare let window: {socket:SocketIOClient.Socket};
import * as React from 'react';
import { Swipeable, EventData } from 'react-swipeable';
import {Circle} from 'rc-progress';
import './Spotify.scss';
import Volume from './components/Volume/Volume';

type propsType = {
    displayTop?: boolean,
}
type stateType = {
    artistName: string,
    songName: string,
    playing: boolean,
    albumCover: string,
    songLength:number,
    currentPos:number,
    currentVolume:number
};

const defaultState: stateType = {
    artistName: "",
    songName: "",
    playing: false,
    albumCover: "",
    songLength:0,
    currentPos:0,
    currentVolume: -1,
}

const PROGRESS_UPDATE_TIME = 1000;
let progressTimer:any = undefined;
let volumeTimer:any = undefined;

class Spotify extends React.Component<propsType, stateType> {
    constructor(props: {}) {
        super(props);
        this.state = defaultState;
        window.socket.on("spotify", (data:any) => {
            if(data.currentTrack.name == undefined){
                this.setState(defaultState);
                return;
            }
            this.setState({
                songName: data.currentTrack.name,
                artistName: data.currentTrack.artists[0].name,
                playing: data.isPlaying,
                albumCover: data.currentTrack.album.images[0].url,
                songLength: data.currentTrack.duration_ms,
                currentPos: data.progressMs,
            });
            clearTimeout(progressTimer);
            this.updateProgress();
        });
    }

    
    forceUpdate(){
        window.socket.emit("spotify", {command:"force_update"});
    }

    updateProgress(){
        clearTimeout(progressTimer);
        if(this.state.songLength != 0){
            progressTimer = setTimeout(() => this.updateProgress(), PROGRESS_UPDATE_TIME);
        }
        if(this.state.currentPos >= this.state.songLength){
            this.forceUpdate();
        }
        this.setState({
            ...this.state,
            currentPos: this.state.currentPos+PROGRESS_UPDATE_TIME
        });
    }

    previousTrack() {
        window.socket.emit("spotify", {command:"previous_track"});
    }

    nextTrack() {
        window.socket.emit("spotify", {command:"next_track"});
    }
    ppTrack() {
        window.socket.emit("spotify", {command:"playpause"});
    }
    handleSwipe(event: EventData){
        if(event.dir == "Right"){
            this.previousTrack();
        } else if(event.dir == "Left"){
            this.nextTrack();
        } else {
            this.ppTrack();
        }
        setTimeout(() => this.forceUpdate(), 500);
    }

    
    volumeChange(initial:number, steps: number){
        this.setState({
            currentVolume: initial+steps,
        });
        clearTimeout(volumeTimer);
        volumeTimer = setTimeout(() => {
            this.setState({currentVolume:-1});
        }, 2000);
    }

    render() {
        return (this.state.playing ? (
            <div className={`spotify-container-container-container ${this.props.displayTop === true ? "top" : ""}`}>
                <div className="spotify-container-container" style={!this.state.playing?{background:'none'}:{background: "-webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(255,255,255,0)), color-stop(7%, rgba(0,0,0,0.07)), color-stop(100%, rgba(0,0,0,1)))"}}>
                    <Swipeable onSwiped={this.handleSwipe.bind(this)} delta={20} className="touch-container">
                        <div className="spotify-container">
                            <div className="image-container">
                                <Circle percent={`${(this.state.currentPos/this.state.songLength)*100}`} strokeWidth="8" strokeColor="#1DB954" trailColor="transparent"/>
                                <img src={this.state.albumCover} alt="" />
                            </div>
                            <div className="info-container">
                                <div className="title">{this.state.currentVolume != -1?
                                `Volume: ${this.state.currentVolume}%` : this.state.songName}</div>
                                <div className="artist">{this.state.artistName}</div>
                            </div>
                        </div>
                    </Swipeable>
                </div>
                <Volume volumeChange={this.volumeChange.bind(this)}/>
            </div>
        ) : (
            <div className="spotify-container-container-container">
                <div className="spotify-container-container">
                        <Swipeable onSwiped={this.handleSwipe.bind(this)} delta={20} className="touch-container">
                            <div></div>
                        </Swipeable>
                </div>
                <Volume volumeChange={this.volumeChange.bind(this)}/>
            </div>
        ));
    }
};

export default Spotify;