declare let window: {socket:SocketIOClient.Socket};
import * as React from 'react';
import './NewsTicker.scss';

type stateType = {
    news:newsObj[],
    currentIndex:number,
    opacity: number,
    displayUrl: string,
}
type newsObj = {
    title:string,
    source:string,
    url:string,
}
const defaultState:stateType = {
    news:[] as newsObj[],
    currentIndex:0,
    opacity: 0,
    displayUrl: null,
};

class NewsTicker extends React.Component<{}, stateType> {
    constructor(props: {}) {
        super(props);
        this.state = defaultState;
        this.clearDisplay = this.clearDisplay.bind(this);
        this.showDisplay = this.showDisplay.bind(this);

        window.socket.on("news", (data:any) => {
            if(!data.articles){
                return;
            }
            let sanitize:newsObj[] = [] as newsObj[];
            for(var i in data.articles){
                sanitize.push({
                    title:data.articles[i].title,
                    source:data.articles[i].source.name,
                    url: data.articles[i].url,
                });
            }
            this.setState({
                news: sanitize,
                currentIndex:0,
            });
        });
    }
    
    componentDidMount(){
        this.animate();
    }
    animate(){
        this.setState({opacity:0});
        setTimeout(()=>{
            this.setState({currentIndex: (this.state.currentIndex+1) % this.state.news.length, opacity:1});
            // this.setState({currentIndex: 2, opacity:1});
            setTimeout(()=>this.animate(), 12000);
        }, 380);
    }
    clearDisplay(){
        this.setState({
            displayUrl:null
        });
    }
    showDisplay(){
        this.setState({
            displayUrl:this.state.news[this.state.currentIndex].url
        });
    }
    
    render() {
        return this.state.news.length > 0 ? (
            <React.Fragment>
            <div className = "news-ticker">
                <div 
                    className="content" 
                    style={{opacity:this.state.opacity}} 
                    onTouchStart={this.showDisplay}
                >
                    <div className="title">{this.state.news[this.state.currentIndex].title}</div>
                    <div className="source">-{this.state.news[this.state.currentIndex].source}</div>
                </div>
            </div>
            {
            this.state.displayUrl != null ? 
            (
            <div className= "news-viewer-container-container" 
                onTouchStart={this.clearDisplay}
            >
                <div className = "news-viewer-container">
                    <div className="iframe" >
                        <iframe 
                        src={`https://cors.io/?${this.state.displayUrl}`}/>
                    </div>
                </div>
            </div>
            ):(null)
            }
            </React.Fragment>
        ): (<div className = "news-ticker"></div>);
    }
};

export default NewsTicker;