declare let window: {socket:SocketIOClient.Socket};
import * as React from 'react';
import './Ticker.scss';

type propsType = {
    temperature?: boolean,
    largeText: boolean,
}
type stateType = {
    strings:string[],
    currentIndex:number,
    opacity: number,
}
const defaultState = {
    //0 is date //1 is quote //2 is weather
    strings:["","",""],
    currentIndex:-1,
    opacity: 0,
};
const UPDATE_DATE_TIME = 60*1000;

class Ticker extends React.Component<propsType, stateType> {
    constructor(props: propsType) {
        super(props);
        this.state = defaultState;
        
        window.socket.on("weather", (weatherData:any) => {
            if(!weatherData.currently || this.props.temperature == false){
                return;
            }
            let oldState: stateType = this.state;
            oldState.strings[2] = `<img class="weather-icon" src="/static/DarkSky/${weatherData.currently.icon}.svg"/> ${Math.round(weatherData.currently.temperature* 10) / 10}&deg;C`
            this.setState(oldState);
        });

        window.socket.on("quote", (quote:string) => {
            let oldState: stateType = this.state;
            oldState.strings[1] = quote;
            this.setState(oldState);
        });
        
    }
    componentDidMount(){
        this.updateDate();
        if(this.props.temperature === false){
            let temp: string[] = this.state.strings.slice(0);
            temp.pop();
            temp.pop();
            this.setState({
                strings: temp
            });
        }
        this.animate();
    }
    animate(){
        this.setState({opacity:0});
        setTimeout(()=>{
            if(this.state.strings[(this.state.currentIndex+1) % this.state.strings.length] != "")
                this.setState({currentIndex: (this.state.currentIndex+1) % this.state.strings.length, opacity:1});
            else 
                this.setState({currentIndex: (this.state.currentIndex+2) % this.state.strings.length, opacity:1});

            // this.setState({currentIndex: 2, opacity:1});
            setTimeout(()=>this.animate(), 8000);
        }, 380);
    }

    updateDate(){
        let oldState: stateType = this.state;
        let date:Date = new Date();
        oldState.strings[0] = this.dateToString(date.getDay(), date.getDate(), date.getMonth(), date.getFullYear());
        this.setState(oldState);
        setTimeout(() => this.updateDate(), UPDATE_DATE_TIME);
    }

    dateToString(day:any,d:any,m:any,y:any){
		let monthArray = [ "January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
		let DayArray = [ "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        let dateString = DayArray[day]+", ";
        
		if(d === 3 || d === 23){
			dateString += d+"rd of ";
		}else if(d === 1 || d === 21 || d === 31){
			dateString += d+"st of ";
		}else if(d == 2 || d == 22){
			dateString += d+"nd of ";
		} else if(d !== 1 || d !== 21 || d !== 31){
			dateString += d+"th of ";
		} 
		dateString += monthArray[m] + " " + y ;
		return dateString;
	}


    render() {
        return (
            <div className = {`ticker ${this.props.largeText ? "large-text":null}`}>
                <div 
                    className="content" 
                    style={{opacity:this.state.opacity}} 
                    dangerouslySetInnerHTML={{ __html: this.state.currentIndex >= 0 ? this.state.strings[this.state.currentIndex]:"&nbsp;"}}
                />
            </div>
        );
    }
};

export default Ticker;