import * as React from 'react';
import './Time.scss';

type stateType = {
    time:string,
    alarm:string,
    alarmVisible: boolean,
};
const TIME_UPDATE_TIME = 10000;
const ALARM_UPDATE_TIME = 1000*30;
let timeout:NodeJS.Timeout = undefined;
class Time extends React.Component<{}, stateType> {
    constructor(props: {}) {
        super(props);
        this.state = {
            time: "00:00",
            alarm:"",
            alarmVisible: false
        }
    }

    componentDidMount(){
        this.updateTime();
        //this.updateAlarms();
    }

    updateTime(){
        clearTimeout(timeout);
        timeout = setTimeout(() => this.updateTime(), TIME_UPDATE_TIME); 
        let currentTime:Date = new Date();
		let currentHours:number = currentTime.getHours();
		let currentMinutes:number = currentTime.getMinutes();		
        let currentHoursString: string = `${currentHours}`;
        let currentMinutesString: string = `${currentMinutes}`;
        if(currentHours < 10){
			currentHoursString = `0${currentHoursString}`;
		}
		if(currentMinutes < 10){
			currentMinutesString = `0${currentMinutesString}`;
        }
        this.setState({
            time:`${currentHoursString}:${currentMinutesString}`
        });
    }
    updateAlarms(){
        setTimeout(() => this.updateAlarms(), ALARM_UPDATE_TIME); 
        fetch("/google-home/alarms")
		.then((response) => {
			if(response.ok)
				return response.json();
		})
		.then((data)=>{
            if(data.alarm.length == 0){
                this.setState({
                    alarm:"",
                });
                return;
            }
            let currentHours:number = data.alarm[0].time_pattern.hour;
            let currentMinutes:number = data.alarm[0].time_pattern.minute;		
            let currentHoursString: string = `${currentHours}`;
            let currentMinutesString: string = `${currentMinutes}`;
            if(currentHours < 10){
                currentHoursString = `0${currentHoursString}`;
            }
            if(currentMinutes < 10){
                currentMinutesString = `0${currentMinutesString}`;
            }
            this.setState({
                alarm:`${currentHoursString}:${currentMinutesString}`
            });
		});
    }
    showAlarm(){
        this.setState({
            alarmVisible: true
        });
    }
    unshowAlarm(){
        this.setState({
            alarmVisible: false
        });
    }
    render() {
        return (
            <div className = "time-container">
                <div className = "time" style={{opacity:this.state.alarmVisible?0.5:1}}>
                    {this.state.alarmVisible? this.state.alarm: this.state.time}
                </div>
                <span className="oi oi-clock show-alarm"
                style={{display:this.state.alarm == ""?"none":"flex"}}
                data-glyph="clock" 
                onTouchStart={this.showAlarm.bind(this)} 
                onTouchEnd={this.unshowAlarm.bind(this)}
                ></span>
            </div>
        );
    }
};

export default Time;