declare let window: {socket:SocketIOClient.Socket};
import * as React from 'react';
import './Wallpaper.scss';

const WALLPAPER_REFRESH_TIME = 60 * 60 * 1000;
type stateType = {
    wallpaperUrl: string,
    opacity:number,
}

class Wallpaper extends React.Component<{}, stateType> {
    constructor(props: {}) {
        super(props);
        this.state = {
            wallpaperUrl:"",
            opacity:0,
        };
        window.socket.on("wallpaper", (data:string) => {
            this.setState({
                opacity:0,
            }, () => setTimeout(() => {
                this.setState({
                    wallpaperUrl: `${data}&w=${screen.width}&h=${screen.height}`,
                }, () => {
                    setTimeout(() => {
                        this.setState({
                            opacity:0.8,
                        });
                    }, 1000);
                });
            }, 500))
            
        })
    }

    render() {
        return (
            <div className = "wallpaper" style={{opacity:this.state.opacity,backgroundImage:`url("${this.state.wallpaperUrl}")`}}>
            </div>
        );
    }
};

export default Wallpaper;