declare let window: {socket:SocketIOClient.Socket};

import * as React from 'react';
import './Volume.scss';
import { timingSafeEqual } from 'crypto';

type stateType = {
    volume: number,
    touchStart: number,
    steps: number,
};
type propsType={
    volumeChange: (initial: number, steps: number)=>void,
}

const defaultState: stateType = {
    volume:0,
    steps:0,
    touchStart: -1
}
const VOLUME_TICK_SIZE = 10;

class Volume extends React.Component<propsType, stateType> {
    constructor(props: propsType) {
        super(props);
        this.state = defaultState;
        this.touchStart = this.touchStart.bind(this);
        this.touchMove = this.touchMove.bind(this);
        this.touchEnd = this.touchEnd.bind(this);
    }

    updateVolume = (data:any) => {
        this.setState({
            volume: data.volume
        });
    }
    componentDidMount() {
        window.socket.on("spotify", this.updateVolume);
    }

    componentWillUnmount() {
        window.socket.removeListener("spotify", this.updateVolume);
    }
    
    touchStart(e: React.TouchEvent){
        this.props.volumeChange(this.state.volume, 0);
        this.setState({touchStart:e.touches[0].clientY});
    }

    touchMove(e: React.TouchEvent){
        e.preventDefault();
        e.stopPropagation();
        var step = Math.floor((this.state.touchStart - e.touches[0].clientY) / VOLUME_TICK_SIZE);
        if(step+this.state.volume > 100 || step+this.state.volume < 0){
            return;
        }
        this.setState({
            steps:step
        }, () => {
            this.props.volumeChange(this.state.volume, this.state.steps);
        });
    }

    touchEnd(e: React.TouchEvent){
        this.setState({
            volume: this.state.volume+this.state.steps,
            steps:0,
            touchStart: -1,
        }, () => {
            window.socket.emit("spotify", {command:"set_volume", arg:{volume: this.state.volume}});
        });
    }

    render() {
        return (
            <div className="volume-touch-container-container">
                <div 
                    onTouchStart={this.touchStart} 
                    onTouchMove={this.touchMove} 
                    onTouchEnd={this.touchEnd} 
                    className="volume-touch-container"
                >

                </div>
            </div>
        );
    }
};

export default Volume;